.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.card-container{
    width: 100%;
    height: auto;
    /* border: 2px solid red; */
    margin: 5% auto;
    text-align: center;
}

.card{
    width: 250px;
    height: 350px;
    border: 1px solid lightgray;
    padding: 10px;
    display: inline-block;
    border-radius: 5px;
    margin: 12px;
    overflow: hidden;
    -webkit-box-shadow: 0px 4px 8px lightgray;
            box-shadow: 0px 4px 8px lightgray;
}

.card h3{
  text-transform: uppercase;
  font-family: cursive
}
.card p::first-letter{
  text-transform: capitalize;
}

.card h5{
  text-decoration: underline
}

#question2{
  width: 60%;
  margin: 0 auto;
  text-align: left
}
code{
  background-color: grey;
  color: white;
  padding: 2px 5px;
  border-radius: 3px;
}
#question2 > span{
  line-height: 2em
}


